.Ambulance-map-box-main {
  display: flex;
  width: 100%;
  padding: 70px 0px;
}

.Ambulance-map-box-main .Ambulance-map-box-child {
  display: flex;
  width: 85%;
  align-items: center;
  justify-content: space-between;
  margin: auto;
}

.Ambulance-map-box-main .Ambulance-map-box-child .Ambulance-map-box-left {
  display: flex;
  width: 52%;
  margin: auto;
  height: 600px;
}

.Ambulance-map-box-main .Ambulance-map-box-child .Ambulance-map-box-left img {
  width: 100%;
}

.Ambulance-map-box-main .Ambulance-map-box-child .Ambulance-map-box-right {
  display: flex;
  width: 39%;
  margin: auto;
  flex-direction: column;
}
.Ambulance-map-box-main .Ambulance-map-box-child .Ambulance-map-box-right h2 {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  /* identical to box height */
  margin-bottom: 20px;
  color: #262626;
}
.Ambulance-map-box-main
  .Ambulance-map-box-child
  .Ambulance-map-box-right
  .body {
  width: 100%;
  height: 578px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  overflow-y: scroll;
}
.Ambulance-map-box-main
  .Ambulance-map-box-child
  .Ambulance-map-box-right
  .body::-webkit-scrollbar {
  width: 5px;
}
.Ambulance-map-box-main
  .Ambulance-map-box-child
  .Ambulance-map-box-right
  .body::-webkit-scrollbar-track {
  width: 5px;
  background-color: #eee;
}
.Ambulance-map-box-main
  .Ambulance-map-box-child
  .Ambulance-map-box-right
  .body::-webkit-scrollbar-thumb {
  width: 5px;
  border-radius: 40px;
  background-color: #1ecbff;
}
