  .Ambulance-map-box-right
  .body
  .Ambulance-mapbox-item {
  padding: 15px 20px;
  height: 79px;
  width: 100%;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  border: 1px solid #e7e7e7;
