  justify-content: space-around;
}
.Ambulance-map-box-main
  .Ambulance-map-box-child
  .Ambulance-map-box-right
  .body
  .Ambulance-mapbox-item
  .righter
  .righter-img {
  border: 1px;
  background: #ffe4cc;
  padding: 10px;
  border-radius: 50%;
  margin-right: 10px;
  position: relative;
}
.Ambulance-map-box-main
  .Ambulance-map-box-child
  .Ambulance-map-box-right
  .body
  .Ambulance-mapbox-item
  .righter
  .righter-img-2
  .pop_over {
  position: absolute;
  padding: 4px 8px;
  margin-left: 8px;
  white-space: nowrap;
  font-size: 0.9rem;
  color: #111;
  font-weight: bold;
  background-color: #f1f1f6;
  border-radius: 4px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.38s;
  z-index: 4;
}
.top_pop {
  top: -90%;
  left: -100%;
}
.pop_right {
  top: 0;
  left: 100%;
}
.top_pop::before {
  top: 100%;
  left: 45%;
}
.pop_right::before {
  top: 50%;
  left: 0;
}
.Ambulance-map-box-main
  .Ambulance-map-box-child
  .Ambulance-map-box-right
  .body
  .Ambulance-mapbox-item
  .righter
  .righter-img-2
  .pop_over
  a {
  text-decoration: none;
  color: #111;
}
.Ambulance-map-box-main
  .Ambulance-map-box-child
  .Ambulance-map-box-right
  .body
  .Ambulance-mapbox-item
  .righter
  .righter-img-2
  .pop_over
  a:hover {
  text-decoration: none;
  color: #111;
}
.Ambulance-map-box-main
  .Ambulance-map-box-child
  .Ambulance-map-box-right
  .body
  .Ambulance-mapbox-item
  .righter
  .righter-img-2:hover
  .pop_over {
  visibility: visible;
  opacity: 1;
}
.Ambulance-map-box-main
  .Ambulance-map-box-child
  .Ambulance-map-box-right
  .body
  .Ambulance-mapbox-item
  .righter
  .righter-img-2
  .pop_over::before {
  content: "";
  position: absolute;
  height: 8px;
  width: 8px;
  transform: rotate(45deg) translateX(-50%);
  background-color: #f1f1f6;
}
.Ambulance-map-box-main
  .Ambulance-map-box-child
  .Ambulance-map-box-right
  .body
  .Ambulance-mapbox-item
  .righter
  .righter-img:hover {
