.doctor {
  padding: 50px 80px;
  width: 100%;
}

.doctor .body {
  width: 100%;
  margin: auto;
}
.doctor .body .heading-body {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.line-design {
  margin-left: 45%;
  margin-bottom: -30px;
}

.heading {
  position: absolute;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  line-height: 160%;
  /* identical to box height, or 77px */

  text-align: center;
  color: #000000;
}
