.Slider {
  display: flex;
  width: 90%;
  align-items: center;
  margin: auto;
}
.LandingPageDepartment a {
  margin-top: 70px;
  margin-right: 15px;
}
.doct-slider-img {
  transition: all 1s;
}

.doct-slider-img:hover {
  transform: translateY(-10px);
}

.Slider::-webkit-scrollbar {
  display: none;
}
@media (min-width: 250px) and (max-width: 450px) {
  .heading {
    font-size: 20px !important;

    /* identical to box height, or 77px */

    /* margin-bottom: -30px !important; */
  }
}

@media (min-width: 250px) and (max-width: 660px) {
  .doctor {
    padding: 0px 20px;
    width: 100%;
  }

  .doctor .body {
    width: 100%;
  }

  .heading {
    font-size: 25px;
    line-height: 130%;
    /* identical to box height, or 77px */
  }
  .heading-body {
    margin-top: 20px;
  }
  .heading-body .heading-background {
    width: 130px;
    height: 100px;
  }
