.my-modal {
  position: absolute;
  left: 50%;
  top: 53%;
  transform: translate(-50%, -50%);
  color: black;
  background: rgb(255, 255, 255);
  padding: 20px 20px;
  border-radius: 10px;
  width: 40%;
}

.my-modal .button-group {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.my-modal .button-group button {
  width: 25px;
  height: 25px;
  font-size: 13px;
}

button.navigation {
  -webkit-appearance: none;
  border: 1.5px solid rgb(136, 136, 136);
  border-style: dashed;
  background: white;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  padding: 2px 6px;
  position: relative;
}

button.navigation.active {
  border-color: rgb(109, 221, 155);
  border-style: none;
  background: rgb(109, 221, 155);
}

button.navigation.active:before,
button.navigation.completed:before {
  content: "";
  width: 130%;
  height: 130%;
  position: absolute;
  left: 0%;
  top: 0%;
  border-radius: 100%;
  background: none;
  z-index: -1;
  border: 2px solid rgb(109, 221, 155);
  transform: translate(-12%, -11%);
}

button.navigation.completed {
  border: none;
}

button.navigation.completed::after {
  background: rgb(109, 221, 155);
  content: "";
  width: 105%;
  height: 105%;
  position: absolute;
  left: 0%;
  top: 0%;
  border-radius: 100%;
  z-index: 11;
  display: flex;
  justify-content: center;
  align-items: center;
}

.separator {
  width: 15px;
  height: 2px;
  background-color: rgb(175, 172, 172);
}
/*RESPONSIVE*/
@media (min-width: 250px) and (max-width: 760px) {
  .my-modal {
    width: 90%;
  }
}
@media (min-width: 761px) and (max-width: 1110px) {
  .my-modal {
    width: 60%;
  }
}
