* {
  font-family: 'Inter', sans-serif;
}
.MainNavbar {
  /* background-color: #fff; */
  padding: 20px 35px;
  display: flex;
  flex: 100%;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  /* border-bottom: 2px solid #e4e4e4; */
}

.MainNavbar .MainLogo {
  display: flex;

  /* flex: 10%; */
  width: 10%;
}

.MainNavbar .MainLogo img {
  width: 80px;
}

.MainNavbar .MainNav-menu {
  display: flex;
  flex: 90%;
  align-items: center;
  justify-content: space-around;
}

.MainNavbar .MainNav-menu .MainNav-cont {
  display: flex;
  flex: 95%;
  align-items: center;
}

.MainNavbar .MainNav-menu .MainNav-cont .Maintopnav-2 {
  display: flex;
  flex: 80%;
  justify-content: center;
  /* overflow: hidden; */
  /* justify-content: flex-start; */
  align-items: center;
}

.MainNavbar .MainNav-menu .MainNav-cont .Maintopnav-2 a {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 160%;
  /* identical to box height, or 26px */

  font-feature-settings: 'salt' on, 'liga' off;

  color: rgba(38, 53, 68, 0.7);

  /* Inside Auto Layout */
  text-decoration: none;
  flex: none;
  order: 1;
  flex-grow: 0;
  margin: 0px 16px;
}

/* Change the color of links on hover */

.MainNavbar .MainNav-menu .MainNav-cont .Maintopnav-2 a:hover {
  color: #008489;
}

/* Add a color to the active/current link */

.MainNavbar .MainNav-menu .MainNav-cont .Maintopnav-2 a.active {
  color: #ff5353;
}
.Mainlogin-section {
  flex: 20%;
  justify-content: flex-end;
  display: flex;
  position: relative;
}
.Mainlogin-section .search-btn {
  padding: 6px 10px;

  background: transparent;
  font-size: 17px;
  border: none;
  cursor: pointer;
}

.Mainlogin-section .login-btn {
  padding: 6px 15px;
  border: none;
  cursor: pointer;
  background: #16292a;
  border-radius: 8px;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  /* identical to box height, or 26px */
  text-align: center;
  font-feature-settings: 'salt' on, 'liga' off;
  color: #ffffff;
  letter-spacing: 0.01rem;
}

.Mainlogin-section .login-btn:hover {
  background-color: #008489;
  transition: 0.3s;
}

.nav-button {
  display: none;
  /* height: 30px;
	width: 28px; */
  background-color: transparent;
}

.Mainlogin-section .user_icon {
  position: relative;
}
.Mainlogin-section .user_icon i {
  font-size: 1.7rem;
}
.Mainlogin-section .user_icon .user_dropdown {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, 30px);
  /* width: 100px; */
  display: flex;
  flex-direction: column;
  background-color: #fff;
  box-shadow: 0 0 16px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  overflow: hidden;
  z-index: 10;
  transition: all 0.48s;
  opacity: 0;
  visibility: hidden;
}
.Mainlogin-section .user_icon:hover .user_dropdown {
  visibility: visible;
  opacity: 1;
  transform: translate(-50%, 0);
}
.Mainlogin-section .user_icon .user_dropdown a {
  text-decoration: none;
  font-size: 1.1rem;
  color: #111;
  border-bottom: 1px solid #999;
}
.Mainlogin-section .user_icon .user_dropdown button {
  border: none;
  outline: none;
  background-color: #fff;
}
.Mainlogin-section .user_icon .user_dropdown * {
  padding: 8px 12px;
  transition: all 0.28s;
}
.Mainlogin-section .user_icon .user_dropdown *:hover {
  background-color: #f1f1f6;
}

@media (min-width: 651px) and (max-width: 1260px) {
  .nav-button {
    border: none;
    justify-content: flex-end;
    display: flex;
    flex: 4%;
    background-color: transparent;
    color: #000;
    font-size: 18px;
    margin-right: 40px;
  }

  .ham-close {
    border: none;
    justify-content: flex-end;
    display: flex;
    flex: 4%;
    background-color: transparent;
    /* height: 30px;
		width: 28px; */
    color: #263544;
    font-size: 20px;
    z-index: 20;
  }

  .MainNavbar .MainNav-menu .MainNav-cont {
    display: none;
  }
  .MainNavbar .MainNav-menu .MainTesting-2 {
    left: 0;
    top: 0;
    margin: 0px;
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: #fff;
    align-items: center;
    justify-content: center;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .MainNavbar .MainNav-menu .MainTesting-2 a {
    float: none;
    display: block;
    text-align: center;
    width: 100%;
    margin: 0px;
    padding: 2px;
    color: #f2f2f2;
    text-decoration: none;
    font-family: Inter;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    /* identical to box height, or 26px */
    color: #263544;
  }

  .MainNavbar .MainNav-menu .MainTesting-2 a:hover {
    color: #008489;
  }

  /* Add a color to the active/current link */

  .MainNavbar .MainNav-menu .MainTesting-2 a.active {
    color: white;
  }
  .MainNavbar .MainNav-menu .MainTesting-2 .search-btn {
    padding: 2px;

    background: transparent;
    font-size: 10px;
    border: none;
    cursor: pointer;
  }
  .MainNavbar .MainNav-menu .MainTesting-2 .login-btn {
    padding: 5px 10px;
    margin-top: 20px;
    font-size: 20px;
    border: none;
    cursor: pointer;
    background: #16292a;
    border-radius: 8px;
    font-family: Inter;
    font-style: normal;
    font-weight: bold;

    /* identical to box height, or 26px */
    text-align: center;
    color: #ffffff;
  }
}

