.container_sec {
  min-height: 100vh;
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .form_container {
    width: 80%;
    padding: 1.5rem 2rem;
    background-color: #fff;
    box-shadow: 0 0 14px rgba(0, 0, 0, 0.1);
    border-radius: 4px;

    h1,
    h2 {
      font-family: Arial, Helvetica, sans-serif;
      font-size: 1.4rem;
      margin-bottom: 1rem;
    }
    input,
    select {
      width: 100% !important;
      padding: 4px 0.7rem;
      background-color: #fff !important;
      outline: none;
      border: 2px solid #ccc;
      border-radius: 3rem;
      margin: 0.5rem 0;
      transition: all 0.27s;
      &[type='checkbox'] {
        width: 15px !important;
        margin-right: 4px;
      }
      &.small {
        // width: 150px;
        margin: 0.5rem 0.4rem;
      }
      &:hover {
        border-color: #999;
      }
      &:focus {
        border-color: #1e90ff;
      }
    }
    .add_btn {
      padding: 0.4rem 0.7rem;
      margin: 0 0.5rem;
      border-radius: 4px;
      outline: none;
      border: none;
      cursor: pointer;
    }
    button[type='submit'] {
      margin: 1rem 0;
      outline: none;
      width: 40%;
      border: 1px solid #fff;
      border-radius: 3rem;
      font-size: 1.04rem;
      padding: 0.5rem 1.2rem;
      color: #fff;
      background-color: #1e90ff;
      cursor: pointer;
    }
  }
}

textarea.about {
  width: 100%;
  min-height: 100px;
  max-height: 140px;
  padding: 5px;
  border-radius: 4px;
}

.create_hospital {
  input,
  select {
    border-radius: 4px !important;
  }
  button {
    border-radius: 4px !important;
  }
}

.icon_box {
  position: relative;
  .icon {
    height: auto;
  }
  .icon_content {
    // width: 200px;
    max-height: 300px;
    overflow-y: scroll;
    position: absolute;
    top: 100%;
    left: 0;
    border-radius: 4px;
    background-color: #f1f1f6;
    border: 1px solid rgb(77, 64, 64);
    box-shadow: 0 0 12px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0;
    margin: 6px 0;
    z-index: 9;
    &::-webkit-scrollbar {
      width: 4px;
      &-thumb {
        width: 4px;
        background-color: #999;
      }
    }
    .col-md-3 {
      padding: 10px 5px;
      display: grid;
      place-items: center;
      transition: all 0.34s;
      &:hover {
        background-color: #fff;
        cursor: pointer;
      }
    }
  }
}

.facility_results {
  width: 100%;
  min-height: 100px;
  border: 1px solid #888;
  padding: 0.4rem;
  border-radius: 4px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  .fas_res {
    max-width: 14rem;
    display: inline-block;
    padding: 0.4rem;
    margin: 4px;
    border-radius: 4px;
    background-color: #f1f1f6;
    position: relative;
    cursor: default;
    img {
      height: 1.5rem;
      width: 1.5rem;
      margin-bottom: 4px;
    }
    p {
      margin: 0;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      &:first-child {
        font-weight: bold;
      }
    }
    .res_close {
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(30%, -20%);
      padding: 0 4px;
      display: inline-block;
      border-radius: 50%;
      border: 1px solid #999;
      background-color: #eee;
      cursor: pointer;
    }
  }
}
