.AdditionalDetails-Main {
  width: 100%;
}
.AdditionalDetails-Main .AdditionalDetails-Body {
  width: 96%;
  margin: auto;
  margin-top: 20px;
}
.AdditionalDetails-Main .AdditionalDetails-Body .Facilities-Details {
  width: 100%;
  padding: 20px;
  display: flex;

  height: 300px;
  flex-direction: column;
  margin-bottom: 20px;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details
  .Facility-Heading {
  display: flex;
  flex-direction: row;
  width: 25%;
}

.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details
  .Facility-Heading
  .texter {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #263544;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details
  .Facility-form {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details
  .Facility-form
  .Facility-form-1 {
  width: 22%;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details
  .Facility-form
  .Facility-form-1
  input,
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details
  .Facility-form
  .Facility-form-3
  input {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #344054;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details
  .Facility-form
  .Facility-form-2
  select {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #344054;
  height: 35px;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details
  .Facility-form
  .Facility-form-2 {
  width: 16%;
}

.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details
  .Facility-form
  .Facility-form-3 {
  width: 42%;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details
  .Facility-form
  .Facility-form-4 {
  margin-top: 26px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 4px 16px;

  width: 97px;
  height: 44px;
  border: none;
  background: #1faf9f;
  border-radius: 8px;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details
  .Facility-form
  .Facility-form-5 {
  margin-top: 26px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 4px 8px;

  width: 48px;
  height: 44px;

  border: 1px solid #344054;
  box-sizing: border-box;
  border-radius: 8px;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details
  .Features-Table {
  overflow-y: auto;
  margin-top: 20px;
  border: 1px solid #9d9d9d;
}
.AdditionalDetails-Main .AdditionalDetails-Body .Procedures {
  width: 100%;
  padding: 20px;
  display: flex;

  height: 120px;
  flex-direction: column;
  margin-bottom: 20px;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Procedures
  .Procedures-Heading {
  display: flex;
  flex-direction: row;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Procedures
  .Procedures-Heading
  .Edit-Icon {
  margin-left: 3px;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Procedures
  .Procedures-Heading
  .Edit-Icon
  img {
  vertical-align: top;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Procedures
  .Procedures-Heading
  .texter {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;

  color: #263544;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Procedures
  .Procedures-Heading-2 {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #9d9d9d;
}
.AdditionalDetails-Main .AdditionalDetails-Body .Procedures .Procedure-form {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Procedures
  .Procedure-form
  .Procedures-form-1 {
  width: 12%;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Procedures
  .Procedure-form
  .Procedures-button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 4px 16px;
  width: 92px;
  height: 40px;
  background: #1faf9f;
  border-radius: 8px;
  border: none;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Procedures
  .Procedure-form
  .UpIcon {
  display: flex;
  justify-content: center;
  align-items: center;
}
.form-label-2 {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;

  color: #9d9d9d;
}
.AdditionalDetails-Main .AdditionalDetails-Body .Facilities-Details-2 {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details-2
  .Back-Button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 4px 8px;

  border: none;
  width: 48px;
  height: 44px;
  background-color: white;
  border: 1px solid #344054;
  box-sizing: border-box;
  border-radius: 8px;
}
.AdditionalDetails-Main
  .AdditionalDetails-Body
  .Facilities-Details-2
  .Save-Button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 4px 8px;

  width: 144px;
  height: 44px;
  margin-right: 10px;
  margin-left: 10px;
  background: #1faf9f;
  border-radius: 8px;
}
