button.navigation {
  -webkit-appearance: none;
  border: 1.5px solid rgb(136, 136, 136);
  border-style: dashed;
  background: white;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  padding: 2px 6px;
  position: relative;
}

button.navigation.active {
  border-color: rgb(109, 221, 155);
  border-style: none;
  background: rgb(109, 221, 155);
}

button.navigation.active:before,
button.navigation.completed:before {
  content: "";
  width: 130%;
  height: 130%;
  position: absolute;
  left: 0%;
  top: 0%;
  border-radius: 100%;
  background: none;
  z-index: -1;
  border: 2px solid rgb(109, 221, 155);
  -webkit-transform: translate(-12%, -11%);
          transform: translate(-12%, -11%);
}

button.navigation.completed {
  border: none;
}

button.navigation.completed::after {
  background: rgb(109, 221, 155);
  content: "";
  width: 105%;
  height: 105%;
  position: absolute;
  left: 0%;
  top: 0%;
  border-radius: 100%;
  z-index: 11;
  display: flex;
  justify-content: center;
  align-items: center;
}

.separator {
  width: 15px;
