  display: flex;
  justify-content: space-between;
}
.CreateForm input[type='file'] {
  color: transparent;
}
.CreateForm input {
  outline: none;
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border-bottom: 1px solid #737b7d;
  -webkit-appearance: none;
  appearance: none;
  border-radius: 0;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

  border-top: transparent;

  border-right: transparent;
  border-left: transparent;
}
.CreateForm .form-select {
  display: block;
  width: 100%;
  padding: 0.375rem 2.25rem 0.47rem 0.75rem;
  padding-top: 0.375rem;
  padding-right: 2.25rem;
  padding-bottom: 0.47rem;
  padding-left: 0.75rem;
  -moz-padding-start: calc(0.75rem - 3px);
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;

  border-bottom: 1px solid #737b7d;
  border-radius: 0;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  -webkit-appearance: none;
  appearance: none;
  border-top: transparent;
  border-left: transparent;
  border-right: transparent;
}
.CreateForm .col-md-6 {
  flex: 0 0 auto;
  width: 48%;
}
.CreateForm .row .col-md-6 {
  flex: 0 0 auto;
  width: 48%;
}
