  position: relative;
  padding: 24px 16px;
  border: 1px solid #e4e4e4;
  cursor: pointer;
  border-style: dashed;
}
.custom-drop-file input {
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 0;
  z-index: 1;
  cursor: pointer;
}
.custom-drop-file img {
