.popup_box {
  position: absolute;
  bottom: 100%;
  right: 50%;
  transform: translate(58%, -10%);
  background-color: #fff;
  border-radius: 10px;
  padding: 10px;
  display: inline-block;
  z-index: 9;
  transition: all 0.34s;
  filter: drop-shadow(0 0 16px rgba(0, 0, 0, 0.3));
  &::before {
    content: '';
    position: absolute;
    bottom: -4px;
    left: 50%;
    height: 20px;
    width: 20px;
    background-color: #fff;
    transform: rotate(45deg) translate(-50%, 50%);
  }
  .heading_close {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    h3 {
      width: 200px;
      font-size: 0.9rem;
      font-weight: 600;
      margin: 0;
    }
    .close {
      width: 30px;
      text-align: center;
      cursor: pointer;
      h2 {
        margin: 0;
      }
    }
  }
  h5 {
    font-size: 1rem;
    margin-top: 5px;
  }
  .contact_buttons {
    display: flex;
    .button {
      height: 38px;
      width: 38px;
      border-radius: 50%;
      background-color: #caf5cc;
      color: #0e6b12;
      display: grid;
      place-items: center;
      margin-right: 0.8rem;
      cursor: pointer;
      &.dir {
        color: #fa8a28;
        background-color: #ffe4cc;
      }
    }
  }
}

.marker {
  position: relative;

  .pointer {
    color: #1ecbff;
    font-size: 2rem;
    height: 28px;
    width: 28px;
    border-radius: 50%;
    background-color: #1ecbff;
    display: grid;
    place-items: center;
    &_active {
      background-color: #8f49a0;
    }
    img {
      height: 50%;
      width: 50%;
    }
  }
}
