.righter-img-mail {
  background: #ffe4cc;
  margin: auto;
  border: 1px;
  padding: 4px;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  color: #ffa451;
}
.column-img {
  display: flex;
  width: 51%;
  flex-direction: row;
}
.righter-img-phone {
  background: #caf5cc;
  margin: auto;
  border: 1px;
  padding: 4px;
  border-radius: 50%;
  width: 36px;
  height: 36px;
}
.boot-table {
  text-align: center;
  margin-top: 1%;
  font-size: 100%;
}

/* responsivity */
@media (min-width: 250px) and (max-width: 500px) {
  .boot-table {
    font-size: 50%;
  }
  .column-img {
    display: flex;
    width: 100%;
    flex-direction: row;
  }
  .column-img .righter-img-mail {
    width: 50%;
    height: auto;
  }
  .column-img .righter-img-mail img {
    height: 19px;
    width: 10px;
  }
  .column-img .righter-img-phone {
    width: 50%;
    height: auto;
  }
  .column-img .righter-img-phone img {
    height: 19px;
    width: 10px;
  }
}
@media (min-width: 250px) and (max-width: 400px) {
  .column-img {
    display: none;
  }
  .boot-table {
    font-size: 50%;
  }
  .boot-table > thead {
    font-size: 15px;
  }
  .organisation-name {
    font-size: 14px;
  }
  .pagination {
    justify-content: center;
  }
}

@media (min-width: 500px) and (max-width: 800px) {
  .boot-table {
    font-size: 65%;
  }
  .pagination {
    justify-content: center;
  }
}
