.CreateForm .image-row .col-md-4 input {
  outline: none;

  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;

  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #737b7d;
  border-top-style: solid;
  border-right-style: solid;
  border-bottom-style: solid;
  border-left-style: solid;
  -webkit-appearance: none;
  appearance: none;
  border-radius: 0;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

  border-style: dashed;
  height: 100px;
  align-items: center;
  display: flex;
}
.custom-drop-file {
  position: relative;
  padding: 24px 16px;
  border: 1px solid #e4e4e4;
  cursor: pointer;
  border-style: dashed;
}
.custom-drop-file input {
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 0;
  z-index: 1;
  cursor: pointer;
}
.custom-drop-file img {
  margin-bottom: 8px;
  width: 40px;
}
.custom-drop-file span {
  margin: 0;
  color: #a4a4a4;
}
.CreateForm .col-12 input {
  outline: none;
  display: block;
  width: 2%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #737b7d;
  -webkit-appearance: none;
  appearance: none;
  border-radius: 0;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.CreateForm .facilities-input {
  display: flex;
  justify-content: space-between;
}
.col-12 .Submit-Button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 16px;

  width: 547px;
  height: 58px;

  background: #8f49a0;
  border-radius: 8px;
}

.icons_show {
  height: 25px;
  padding-bottom: 5px;
}

.rept_res {
  width: 100%;
  min-height: 150px;
  border: 1px solid #999;
  border-radius: 4px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 0.5rem;
  padding: 7px;
}

.rept_res .dept {
  display: flex;
  align-items: center;
  padding: 2px 10px;
  margin: 0 4px;
  background-color: #eee;
  border-radius: 4px;
}

.rept_res .dept p,
.rept_res .dept h4 {
  margin: 0;
  text-transform: capitalize;
}
.rept_res .dept h4 {
  padding: 4px;
  cursor: pointer;
}
