.service-1 {
  padding: 40px 0px;
}

.service-1 .section-text h1 {
  font-size: 50px;
}

.service-1 .section-text .para {
  width: 100%;
}

.service-1 .section-text .para h5 {
  width: 50%;
  margin: auto;
  color: #484848;
  font-weight: 500;
  font-size: 18px;
  line-height: 180%;
}

.service-1 .section-text .para h5 span {
  -webkit-animation: glowingg 1500ms infinite;
  -moz-animation: glowingg 1500ms infinite;
  -o-animation: glowingg 1500ms infinite;
  animation: glowingg 1500ms infinite;
  font-weight: 600;
}

@-webkit-keyframes glowingg {
  0% {
    background-color: #00acb2;
    -webkit-box-shadow: 0 0 3px #00acb2;
  }
  50% {
    background-color: #8f49a0;
    -webkit-box-shadow: 0 0 40px #8f49a0;
  }
  100% {
    background-color: #00acb2;
    -webkit-box-shadow: 0 0 3px #00acb2;
  }
}

@-moz-keyframes glowingg {
  0% {
    color: #00acb2;
  }
  50% {
    color: #8f49a0;
  }
  100% {
    color: #00acb2;
  }
}

@-o-keyframes glowingg {
  0% {
    color: #00acb2;
  }
  50% {
    color: #8f49a0;
  }
  100% {
    color: #00acb2;
  }
}

@keyframes glowingg {
  0% {
    color: #00acb2;
  }
  50% {
    color: #8f49a0;
  }
  100% {
    color: #00acb2;
  }
}

.service-1 .section-text .hint {
  background-color: white;
  border: 2px solid #00acb2;
  color: #00acb2;
  font-weight: 600;
  padding: 5px 15px;
  margin-top: 15px;
}

.service-3 {
  padding-top: 40px;
}

.service-3 .section-text h1 {
  font-size: 50px;
}

.service-3 .section-text h5 {
  color: #484848;
  font-weight: 500;
  font-size: 20px;
  line-height: 180%;
  margin-bottom: -80px;
}

.service-1 .section-title .section-text .bar {
  margin: auto;
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  width: 47%;
  border: none;
}

/* .service-1 .section-title .section-text .bar:hover{
    box-shadow: 1px 1px 8px 1px #dcdcdc;
} */

/* .service-1 .section-title .section-text .bar:focus-within{
    box-shadow: 1px 1px 8px 1px #dcdcdc;
    outline:none;
  } */
.service-1 .section-title .section-text .bar .searchbar {
  height: 45px;
  border: none;
  width: 80%;
  font-size: 16px;
  outline: none;
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  border: 1px solid #dcdcdc;
}

.service-1 .section-title .section-text .bar .search-button {
  background: #1f1f1f;
  border: none;
  align-items: center;
  width: 20%;
  color: #ffffff;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  /* margin-left: 7%; */
  height: 45px;
  transition: all 0.3s;
}

.service-1 .section-title .section-text .bar .search-button:hover {
  background-color: #00acb2;
}

.modal-box {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 37%;
  background-color: #fff;
  padding: 40px;
  padding-top: 30px;
  padding-bottom: 20px;
  text-align: center;
  max-height: 90%;
  overflow-y: scroll;
}

.modal-box h2 {
  margin-bottom: 20px;
  font-size: 25px;
}

.modal-box h4 {
  text-align: left;
  font-size: 20px;
}

.modal-box p {
  margin-top: 10px;
  margin-bottom: 20px;
  text-align: left;
}

.modal-image {
  width: 90%;
  margin-bottom: 20px;
}

.slider {
  padding: 25px 80px 80px;
  border-top: 1px solid white;
  background: repeating-linear-gradient(
    #fff,
    #fff 31.1%,
    #16292a 30.9%,
    #16292a 100%
  );
  width: 100%;
  align-items: center;
}

.card-slider {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
}

.card-slider::-webkit-scrollbar {
  display: none;
}

.card-1 {
  flex: 0 0 auto;
  background-color: white;
  text-align: left;
  position: relative;

  height: 550px;
  width: 95%;
  margin: auto;
  padding-bottom: 30px;
  overflow: hidden;
}
.card-1 .card-partition {
  width: 100%;
  align-items: center;
  justify-content: space-between;
  display: flex;
}
.card-1 .card-partition .explore-button {
  display: flex;
  flex-direction: row;
  width: 55%;
}
.card-partition .card-content-new {
  width: 50%;
  font-weight: bold;
}
.blingy-button .blingy {
  background-color: #004a7f;
  -webkit-border-radius: 10px;
  border-radius: 10px;
  border: none;
  color: #ffffff;
  cursor: pointer;
  display: inline-block;
  font-family: Arial;
  font-size: 20px;
  padding: 5px 10px;
  text-align: center;
  text-decoration: none;
  -webkit-animation: glowing 1500ms infinite;
  -moz-animation: glowing 1500ms infinite;
  -o-animation: glowing 1500ms infinite;
  animation: glowing 1500ms infinite;
}
@-webkit-keyframes glowing {
  0% {
    background-color: #00acb2;
    -webkit-box-shadow: 0 0 3px #00acb2;
  }
  50% {
    background-color: #8f49a0;
    -webkit-box-shadow: 0 0 40px #8f49a0;
  }
  100% {
    background-color: #00acb2;
    -webkit-box-shadow: 0 0 3px #00acb2;
  }
}

@-moz-keyframes glowing {
  0% {
    background-color: #00acb2;
    -moz-box-shadow: 0 0 3px #00acb2;
  }
  50% {
    background-color: #8f49a0;
    -moz-box-shadow: 0 0 40px #8f49a0;
  }
  100% {
    background-color: #00acb2;
    -moz-box-shadow: 0 0 3px #00acb2;
  }
}

@-o-keyframes glowing {
  0% {
    background-color: #00acb2;
    box-shadow: 0 0 3px #00acb2;
  }
  50% {
    background-color: #8f49a0;
    box-shadow: 0 0 40px #8f49a0;
  }
  100% {
    background-color: #00acb2;
    box-shadow: 0 0 3px #00acb2;
  }
}

@keyframes glowing {
  0% {
    background-color: #00acb2;
    box-shadow: 0 0 3px #00acb2;
  }
  50% {
    background-color: #8f49a0;
    box-shadow: 0 0 40px #8f49a0;
  }
  100% {
    background-color: #00acb2;
    box-shadow: 0 0 3px #00acb2;
  }
}
@media screen and (max-width: 1150px) {
  .card-1 {
