.SingleImageUploader-Main {
  width: 90%;
  display: flex;
  margin: auto;
}
.SingleImageUploader-Main .uploader-body {
  background: #d0d5dd;
  width: 240px;
  height: 240px;
  border-radius: 50%;
  position: relative;
  margin: auto;
}
.SingleImageUploader-Main .uploader-body img {
  position: absolute;
  top: 100px;
  left: 100px;
}
.SingleImageUploader-Main .uploader-body .Image-List {
  height: 240px;
  width: 240px;
}
.SingleImageUploader-Main .uploader-body .Image-List .image-item {
  height: 240px;
  width: 240px;
}
.SingleImageUploader-Main .uploader-body .Image-List .image-item img {
  height: 240px;
  width: 240px;
  border-radius: 50%;
  position: absolute;
  top: 0px;
  left: 1px;
}
.SingleImageUploader-Main
  .uploader-body
  .Image-List
  .image-item__btn-wrapper
  button {
  position: absolute;
  left: 178px;
  top: 20px;
  border-radius: 50%;
  color: red;
  border: aliceblue;
  background-color: aliceblue;
}
