.signup_pages {
  height: 500px;
  width: 100%;
  overflow: hidden;
  overflow-y: scroll;
}

.signup_pages::-webkit-scrollbar {
  display: none;
}

.form-header,
.second-header {
  display: flex;
  color: #333;
  justify-content: flex-start;
  align-items: center;
}

.form-header .form-heading-page-1{
  margin-left: 30px;
}

.second-header {
  align-items: flex-start;
  flex-direction: column;
  margin: 25px 0;
  margin-left: 40px;
}

.second-header h6 {
  color: rgb(54, 211, 7);
  font-size: 16px;
}
.second-header p {
  font-size: 14px;
  opacity: 0.7;
  margin-top: 7px;
}

.icon {
  width: 1px;
  height: 3px;
  padding-left: 5px;
  margin: 0 5px;
  opacity: 0.6;
}

.icon.back {
  margin-right: 20px;
  margin-left: 0;
}

.icon.back:hover{
  cursor: pointer;
}

/* Input style */

.input-group {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  flex-direction: column;
  margin-left: 40px;
}

.input {
  border: 1px solid rgba(51, 51, 51, 0.699);
  outline: none;
  background: none;
  color: black;
  padding: 10px 20px;
  margin-bottom: 15px;
  border-radius: 5px;
  width: 90%;
}

.input::placeholder {
  color: rgba(51, 51, 51, 0.699);
}

button.continue {
  outline: none;
  border: none;
  cursor: pointer;
  background: #1fb6ba;
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  width: 90%;
  margin-top: 26px;
}

/* Second Page style */

.second-page-fields {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 70%;
  margin: 0;
  padding: 0px;
}

.input-second-page {
  width: 300px;
}

.input-second-page-2 {
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
}
.input-second-page-2 .age {
  width: 40%;
  margin-right: 10px;
}

.input-second-page-2 .gender {
  width: 45%;
  margin-right: 5px;
}

.input-group-2 {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.profile-pic {
  width: 150px;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  background: rgb(179, 179, 179);
  margin-bottom: 20px;
}

.profile-pic .profile-add {
  padding-left: 0;
  color: #555;
  font-size: 50px;
  width: 200px;
}

.second-page-preferences {
  display: flex;
  flex-direction: row;
  align-items: start;
  justify-content: space-evenly;
  flex-flow: row;
}

.second-page-preferences .input {
  margin-right: 20px;
  margin-top: 20px;
  color: #777;
}
.second-page-section .continue {
  left: 50%;
  position: relative;
  transform: translate(-50%, -50%);
  width: 85%;
}
.second-page-preferences .input[type="email"] {
}

.add-profile-pic {
  position: relative;
  width: 100%;
  cursor: pointer;
  color: #777;
}

.add-circle {
  position: absolute;
  left: 70%;
  top: 52%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  color: #777;
}
.admin-select {
  color: #555;
  font-size: 13px;
  width: 142%;
  text-align: center;
}
.admin-select .left-span {
  float: left;
}
.admin-select .right-span {
  padding-left: 13%;
}

.third-page-preferences {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-around;
  margin-top: 20px;
  width: 50%;
}
.third-page-preferences > select {
  margin-right: 20px;
  color: #666;
}

.thankyou {
  margin: 50px;
}

.reset-all {
  padding: 10px 20px;
  background: rgb(202, 18, 18);
  color: white;
  border-radius: 4px;
  outline: none;
  border: none;
}
.input-group .thirdpage-second-info {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 83%;
}
@media (min-width: 250px) and (max-width: 966px) {
  .second-page-fields {
    display: flex;
    flex-wrap: wrap;
  }
  .second-page-preferences {
    display: flex;
    flex-wrap: wrap;
  }
  .input-group-2 {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-left: 30px;
  }
}
@media (min-width: 250px) and (max-width: 500px) {
  .second-header {
    margin-left: 0px;
  }
  .signup_pages .input-group {
    margin-left: 0px;
  }
  .form-header .form-heading-page-1{
    margin-left: 0px;
  }
}
