.input_box_form {
  width: 100%;
}
.input_box_form_num {
  width: 10%;
}
.input_box_form_num_full {
  width: 80%;
}
.hospitalinfo_container {
  width: 100%;
}
.hospitalinfo_col {
  padding-top: 10px;
}
.hospitalinfo_row {
  width: 100%;
}
.fas_res {
  display: inline-block;
  padding-right: 15px;
}
.hospital-type-dropdown {
  width: 100%;
  display: block;
  height: 57%;
  border: 1px solid rgb(219, 219, 219);
  border-radius: 5px;
}
.hospitalinfo_col {
  font-family: Inter;
  font-style: medium;
  font-size: 14px;
  line-height: 17px;
  text-align: left;
  vertical-align: top;
  color: #9d9d9d;
}
.input_box_form {
  margin-top: 5px;
}
.doctor_details_heading {
  font-family: Inter;
  font-style: semi-bold;
  font-size: 14px;
  line-height: 20px;
  text-align: left;
  vertical-align: top;
  color: #9d9d9d;
}

.table_heading,
.table_body {
  font-family: Inter;
  font-size: 14px;
  line-height: 20px;
  text-align: left;
  vertical-align: top;
  color: #4c4c4c;
}
.table_body {
  font-style: Regular;
}
.table_heading {
  font-style: semi-bold;
}
.Department-Dropdown {
  width: 75%;
  height: 35px;
  margin-left: 11px;
  border-bottom-right-radius: 0px;
  border-top-right-radius: 0px;
}
.Department-Dropdown-Button {
  color: white;
  width: 10%;
  background-color: rgb(232, 232, 232);
  border-color: rgb(232, 232, 232);
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Page-End-Save-Button {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  margin-bottom: 20px;
  width: 100%;
}
.Page-End-Save-Button .Save-details {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 4px 8px;
  background: #1faf9f;
  border-radius: 8px;
  width: 144px;
  height: 48px;
  border: none;
}
.Page-End-Save-Button .Save-details .button-text {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */

  display: flex;
  align-items: center;

  color: #ffffff;
}
.Page-End-Save-Button .Page-End-Arrow {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 4px 8px;

  width: 48px;
  height: 48px;
  margin-left: 20px;
  border: 1px solid #344054;
  box-sizing: border-box;
  border-radius: 8px;
}
