a[href^="http://maps.google.com/maps"]
{
  display: none !important;
}
a[href^="https://maps.google.com/maps"]
{
  display: none !important;
}

.gmnoprint a,
.gmnoprint span,
.gm-style-cc {
  display: none;
}
.gmnoprint div {
  background: none !important;
}

.nav-line {
  border: none;
  margin-top: 0px;
  height: 1px;
  background-color: #5c5959;
  margin-bottom: -10px;
  z-index: 4;
}

.herosection_main {
  width: 100%;
  background: #fff;
}

.heroSection {
  width: 90%;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 50px;
  background: #fff;
}

.left {
  width: 49%;
  display: flex;
  flex-direction: column;
}

.right {
  display: flex;
  flex-direction: column;
  width: 49%;
}

.heroSection .left .col2 {
  width: 100%;
  display: flex;
}

.heroSection .left .colSlider {
  margin-top: 2%;
  width: 100%;
  display: flex;
}

.col1 {
  display: flex;
  flex-direction: column;
}

.imgSm1 {
  width: 160px;
  height: 180px;
  margin: 10px;
}

.imgSm2 {
  width: 160px;
  height: 180px;
  margin: 10px;
  opacity: 0.3;
}

.img {
  /* text */
  position: relative;
  text-align: center;
  font-family: Raleway;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;
  display: flex;
  align-items: center;
  font-feature-settings: "pnum" on, "lnum" on;

  color: #000000;

  cursor: pointer;
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.imgLg {
  width: 100%;
  height: 330px;
}
.imgLg img {
  width: 98%;
  height: 330px;
  margin: auto;
}

.top {
  padding-bottom: 10px;
}

.greyish {
  text-transform: uppercase;
  height: 17px;

  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 64px;
  /* or 400% */

  display: flex;
  align-items: center;
  letter-spacing: -0.01em;

  color: #adadad;
  margin-bottom: 10px;
}

.title {
  width: 100%;

  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 35px;
  line-height: 48px;
  display: flex;
  align-items: center;
  letter-spacing: -0.01em;

  color: #262626;
}

.middle {
  margin-top: 30px;
  width: 100%;
  height: auto;
  display: flex;
}

.menus1 {
  display: flex;
  flex-direction: row;
}

.menu1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: auto;
  background: #eafeff;
  margin-right: 10px;
  width: 100%;
  justify-content: space-around;
  margin-bottom: 20px;
}

.middle .middle-left {
  display: flex;
  justify-content: center;
}

@media (min-width: 320px) and (max-width: 859px) {
  .middle {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.middle .middle-left .description {
  width: 100%;
  margin: auto;

  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 32px;
  /* or 200% */

  font-feature-settings: "pnum" on, "lnum" on;

  color: #000000;

  opacity: 0.8;
}

.middle .middle-left .description .my-anchor-css-class {
  color: #00acb2;
  text-decoration: none;
  font-weight: 500;
}

