.bottom {
  display: flex;
  flex-direction: column;
}

.title1 {
  margin-top: 10px;
  margin-bottom: 10px;

  width: 72px;
  height: 19px;

  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  display: flex;
  align-items: center;
  letter-spacing: -0.01em;

  color: #262626;
}

.menus1 {
  display: flex;
  flex-direction: row;
}

.menu1 {
  display: flex;
  flex-direction: row;

  align-items: center;
  padding: 8px;

  width: 168.6px;
  height: 44.6px;

  background: #eafeff;

  margin-right: 10px;
}

.feature {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */

  display: flex;
  align-items: center;

  color: #262626;
}

.iconDetail {
  color: #00acb2;
  margin-right: 8px;
}

.contact {
  margin-top: 0px;
  display: flex;
  flex-direction: row;
}

.contact1 {
  display: flex !important;
  flex-direction: row;
  padding: 18px 30px !important;
  background: #00acb2;
  border-radius: 30px !important;
  font-family: Raleway;
  font-style: normal;
  font-weight: 600 !important;
  font-size: 17px;
  line-height: 21px;
  color: #ffffff;
  border: none !important;
  margin-right: 25px;
}

.contact2 {
  display: flex;
  flex-direction: row;
  padding: 10px 20px;

  background: #e9e9e9;
  border-radius: 30px;
  border: none;
  font-family: Raleway;
  font-style: normal;
  font-weight: 600;
  font-size: 17px;
  line-height: 21px;
  align-items: center;
  color: #777777;
  transition: all 0.27s;
}
.contact2:hover {
  color: #fff;
  background-color: rgb(88, 207, 211);
}
.contact2:hover .icon3 {
  color: #fff;
}

.icon2 {
  color: #ffffff;
  margin-right: 8px;
}

.icon3 {
  color: #777777;
  margin-right: 8px;
  font-size: 30px !important;
}

