@media only screen and (max-width: 1155px) {
  .heroSection {
    padding: 40px 50px;
    flex-wrap: wrap;
  }
  .menus1 {
    flex-wrap: wrap;
  }
  .menu1 {
    margin: 20px;
  }
  .contact {
    flex-wrap: wrap;
  }

  .title {
    margin-top: 15px;
  }

  .right {
    width: 100%;
    margin-left: 0px;
    margin-top: 40px;
  }
  .heroSection .left {
    width: 100%;
    height: 500px;
  }

  .contact {
    margin-top: -5px;
    margin-bottom: -10px;
    margin-left: -20px !important;
    display: flex;
    flex-direction: row;
  }
}

@media only screen and (max-width: 730px) {
  .col1 {
    display: none;
  }
  .col2 {
    display: none;
  }
  .imgCarousel {
    object-fit: cover;
  }

  .carousel {
    /* max-width: 400px; */
    width: 100%;
  }

  .left {
    padding: 0px 0px;
  }
}

@media only screen and (min-width: 730px) {
  .carousel {
    display: none;
  }
}

