.service-content h3 {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 32px;
  /* identical to box height, or 133% */

  text-align: center;
  font-feature-settings: "salt" on, "liga" off;

  /* Text / Gray 900 */

  color: #18191f;
}
.service-box {
  box-sizing: border-box;
}
.service-2 .service-class .service-box-border .service-content {
  margin-top: 15%;
}
.service-2 .service-class .service-box-border {
  padding: 10% 10%;
  height: 350px;
  width: 300px;
  margin: auto;
}
.service-2 .service-class .service-box-border .service-content p {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 26px;
  /* or 162% */

  text-align: center;
  font-feature-settings: "salt" on, "liga" off;

  /* Text / Gray 900 */

  color: #18191f;
}
.service-2 {
  margin-top: 7%;
}

.service-2 .title {
  height: auto;
  width: 82%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}

.service-2 .title .left {
  display: flex;
  width: 95%;
}
.service-2 .title .left h1 {
  font-family: Inter;
  font-style: normal;
  font-weight: 800;
  font-size: 100%;
  line-height: 133%;
  /* or 133% */

  text-align: center;
  font-feature-settings: "salt" on, "liga" off;

  /* Text / Gray 900 */

  color: #18191f;
}

.service-2 .title .right {
  margin-top: 0px;
  display: flex;
  width: 95%;
  margin-left: 9%;
  margin-right: 7%;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 30px;
  /* or 178% */
  text-align: center;
  font-feature-settings: "salt" on, "liga" off;

  /* Text / Gray 900 */

  color: #18191f;
}

@media (min-width: 250px) and (max-width: 500px) {
  .service-2 .row {
    margin: auto;
  }
  .service-content h3 {
    font-family: Inter;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 32px;
    /* identical to box height, or 133% */

    text-align: center;
    font-feature-settings: "salt" on, "liga" off;

    /* Text / Gray 900 */

    color: #18191f;
  }
  .service-box {
    border: 1px solid #f4f4f4;
    box-sizing: border-box;
  }

  .service-2 .title {
    width: 95%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 40px;
    height: auto;
  }

  .service-2 .title .left {
    height: auto;
    width: 100%;
  }

  .service-2 .title .right {
    width: 100%;
    height: auto;
    margin-left: 1%;
  }

  .service-2 .title .left h1 {
    text-align: center;
    font-size: 28px;
    /* margin-bottom: -30px; */
  }

  .service-2 .title .right p {
    font-size: 16px;
    line-height: 30px;
  }
}

@media (min-width: 510px) and (max-width: 800px) {
  .service-2 .row {
    margin: auto;
  }
  .service-content h3 {
    font-family: Inter;
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 32px;
    /* identical to box height, or 133% */

    text-align: center;
    font-feature-settings: "salt" on, "liga" off;

    /* Text / Gray 900 */

    color: #18191f;
  }
  .service-box {
    border: 1px solid #f4f4f4;
    box-sizing: border-box;
  }

  .service-2 .title {
    width: 95%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 40px;
  }

  .service-2 .title .left {
    height: auto;
    width: 100%;
  }

  .service-2 .title .right {
    width: 100%;
    height: auto;
    margin: auto;
  }

  .service-2 .title .left h1 {
    text-align: center;
    font-size: 40px;
    margin-bottom: 30px;
  }

  .service-2 .title .right p {
    font-size: 16px;
    line-height: 30px;
  }
}

@media (min-width: 810px) and (max-width: 1300px) {
  .service-2 .title {
    height: auto;
    width: 82%;
    margin: auto;
    display: flex;
    align-items: center;
    margin-bottom: 70px;
  }
}
@media (min-width: 1279px) {
  .service-2 .service-class {
    width: 82%;
    margin: auto;
  }
}
