.AmbulanceSearchbarComponent .Ambulance-Bottom {
  width: 100%;
  margin-top: 30px;
}
.AmbulanceSearchbarComponent .Ambulance-Bottom .Searchbar-Body {
  background-color: white;
  width: 55%;
  margin: auto;

  border: 2px solid #e4e1e1;
  box-sizing: border-box;
  border-radius: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 15px 10px 30px;
}
.AmbulanceSearchbarComponent
  .Ambulance-Bottom
  .Searchbar-Body
  .Searchbar-Items {
  display: flex;
  align-items: center;
  border-right: 1.5px solid #b0b0b0;
  width: 80%;
  justify-content: space-evenly;
}
.AmbulanceSearchbarComponent
  .Ambulance-Bottom
  .Searchbar-Body
  .Searchbar-Items
  .LeftItem {
  display: flex;
  flex-direction: column;
  width: 82%;
}
.AmbulanceSearchbarComponent
  .Ambulance-Bottom
  .Searchbar-Body
  .Searchbar-Items
  .LeftItem
  h3 {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  /* identical to box height */

  color: #686868;
}
.AmbulanceSearchbarComponent
  .Ambulance-Bottom
  .Searchbar-Body
  .Searchbar-Items
  .LeftItem
  input {
  border: none;
  overflow: auto;
  outline: none;

  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;

  resize: none;

  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14.22px;
  line-height: 17px;

  color: #b0b0b0;
}
.AmbulanceSearchbarComponent
  .Ambulance-Bottom
  .Searchbar-Body
  .Searchbar-Items
  .LeftItem
  input::placeholder {
  font-size: 14.22px;
  line-height: 17px;

  color: #b0b0b0;
}
.AmbulanceSearchbarComponent
  .Ambulance-Bottom
  .Searchbar-Body
  .Searchbar-Items
  .RightItem {
  width: 5%;
  color: orange;
}
.AmbulanceSearchbarComponent
  .Ambulance-Bottom
  .Searchbar-Body
  .Searchbar-Button-Main {
  width: 20%;
  justify-content: flex-end;
  display: flex;
}

.AmbulanceSearchbarComponent
  .Ambulance-Bottom
  .Searchbar-Body
  .Searchbar-Button-Main
  .Searchbar-Button {
  width: 88%;
  display: flex;
  align-items: center;
  padding: 7px 15px;
  background: #1f1f1f;
  color: #fff;
  border-radius: 48px;
  cursor: pointer;
  margin-right: 0px;
  justify-content: space-around;
}
.AmbulanceSearchbarComponent
  .Ambulance-Bottom
  .Searchbar-Body
  .Searchbar-Button-Main
  .Searchbar-Button
  h4 {
  font-weight: 600;
  font-size: 12px;
  margin-top: 7px;
}
.AmbulanceSearchbarComponent
  .Ambulance-Bottom
  .Searchbar-Body
  .Searchbar-Button-Main
  .Searchbar-Button
  .search-icon {
  font-size: 20px;
}
/* Responsiveness*/
@media (min-width: 710px) and (max-width: 1000px) {
  .AmbulanceSearchbarComponent .Ambulance-Bottom .Searchbar-Body {
    flex-wrap: wrap;
  }
  .AmbulanceSearchbarComponent
    .Ambulance-Bottom
    .Searchbar-Body
    .Searchbar-Button-Main {
    width: 24%;
    justify-content: flex-end;
    display: flex;
  }
  .AmbulanceSearchbarComponent
    .Ambulance-Bottom
    .Searchbar-Body
    .Searchbar-Items {
    width: 48%;
    border-right: none;
  }
}
@media (min-width: 550px) and (max-width: 711px) {
  .AmbulanceSearchbarComponent .Ambulance-Bottom .Searchbar-Body {
    flex-wrap: wrap;
    border-radius: 20px;
    padding-top: 20px;
    padding-left: 10px;
  }
  .AmbulanceSearchbarComponent
    .Ambulance-Bottom
    .Searchbar-Body
    .Searchbar-Button-Main {
    width: 30%;
    justify-content: center;
    display: flex;
  }

  .AmbulanceSearchbarComponent
    .Ambulance-Bottom
    .Searchbar-Body
    .Searchbar-Items {
    width: 48%;
    border-right: none;
    padding-bottom: 5px;
  }
}
@media (min-width: 450px) and (max-width: 551px) {
  .AmbulanceSearchbarComponent .Ambulance-Bottom .Searchbar-Body {
    flex-direction: column;
    padding-top: 20px;
    padding-left: 10px;
    border-radius: 15px;
    margin-top: -10px;
  }
  .AmbulanceSearchbarComponent
    .Ambulance-Bottom
    .Searchbar-Body
    .Searchbar-Items {
    width: 88%;
    border-right: none;
    padding-bottom: 5px;
    margin-bottom: 10px;
  }
  .AmbulanceSearchbarComponent
    .Ambulance-Bottom
    .Searchbar-Body
    .Searchbar-Button-Main {
    width: 50%;
    justify-content: center;
    display: flex;
  }
}
@media (min-width: 250px) and (max-width: 451px) {
  .AmbulanceSearchbarComponent .Ambulance-Bottom .Searchbar-Body {
    flex-direction: column;
    padding-top: 20px;
    padding-left: 10px;
    border-radius: 15px;
    margin-top: -10px;
  }

  .AmbulanceSearchbarComponent
    .Ambulance-Bottom
    .Searchbar-Body
    .Searchbar-Items {
    width: 97%;
    border-right: none;
    padding-bottom: 5px;
    margin-bottom: 10px;
  }
  .AmbulanceSearchbarComponent
    .Ambulance-Bottom
    .Searchbar-Body
    .Searchbar-Button-Main {
    margin-top: 10px;
    width: 60%;
    justify-content: center;
    display: flex;
  }
}
