.AmbulanceBanner-Main {
  width: 100%;
  display: flex;
  min-height: 320px;
  background: url("../img/unsplash_ejWJ3a92FEs.png");
  background-position: center;
  background-size: contain;
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: left top;
}

.AmbulanceBanner-Main .AmbulanceBanner-body {
  width: 70%;
  margin: auto;
}
.AmbulanceBanner-Main .AmbulanceBanner-body .AmbulanceBanner-heading {
  width: 50%;
  margin: auto;
  font-family: Inter;
  font-style: normal;
  font-weight: 700;
  font-size: 45px;
  line-height: 140%;

  text-align: center;
  letter-spacing: 0.08rem;
  font-feature-settings: "salt" on, "liga" off;

  color: #ffffff;
}
.AmbulanceBanner-Main .AmbulanceBanner-body .AmbulanceBanner-heading-2 {
  font-family: Inter;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  /* identical to box height */
  text-align: center;
  color: #ffffff;
  margin-top: 30px;
  letter-spacing: 0.07rem;
}
@media screen and (max-width: 270px) {
  .AmbulanceBanner-Main {
    background-position: center;
    background-size: contain;
    background-size: auto 100vh;
    background-repeat: no-repeat;
    background-position: center;
    min-height: 500px;
  }
  .AmbulanceBanner-Main .AmbulanceBanner-body {
    width: 100%;
    margin: 0;
    align-items: center;
  }
  .AmbulanceBanner-Main .AmbulanceBanner-body .AmbulanceBanner-heading {
    font-weight: 600;
    font-size: 22px;
    line-height: 1.5rem;
    letter-spacing: 0.05em;
    text-align: center;
    margin-top: 53%;
    margin-bottom: 2%;
  }
}
@media (min-width: 270px) and (max-width: 350px) {
