@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&family=Fruktur&family=Poppins:wght@200&family=Source+Sans+Pro:wght@300;400;600;700&display=swap");

* {
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: small;
  margin: 8%;
}
.header h2 {
  padding-right: 5px;
}
.header .icons {
  color: #888;
  font-size: medium;
}

section .contentBx {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
section .contentBx .formBx {
  width: 100%;
  border-radius: 5px;
  padding: 20px;
  background: rgb(255, 255, 255);
  box-shadow: 0 0 2px rgba(77, 70, 70, 0), 0 0 4px rgba(77, 70, 70, 0),
    0 0 6px rgba(77, 70, 70, 0), 0 0 8px rgba(77, 70, 70, 0);
  position: relative;
}
section .contentBx .inputBx {
  padding-left: 10%;
  margin-bottom: 20px;
}

section .contentBx .inputBx input {
  width: 90%;
  padding: 10px 20px;
  outline: none;
  font-weight: 400;
  border: 1px solid #777;
  border-radius: 11px;
  position: relative;
}
section .contentBx .inputBx button {
  width: 80%;
  padding: 10px 20px;
  outline: none;
  color: white;
  background-color: #179fa6;
  font-size: 15px;
  font-weight: bold;
  border: 1px solid #179fa6;
  border-radius: 10px;
  cursor: pointer;
  margin-left: 5%;
}
section .contentBx .inputBx p {
  color: #777;
  font-size: small;
  padding-bottom: 20px;
  /* padding-left: 40px; */
  text-align: center;
}

section .contentBx .inputBx button[type="submit"]:hover {
  background-color: #26b5bd;
}
section .contentBx .inputBx a {
  color: #780ead;
  font-weight: bold;
  cursor: pointer;
}
.eye {
  color: #777;
  cursor: pointer;
  margin-left: -35px;
  margin-top: 2px;
  z-index: 111;
}

.inputBx.password-field {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

@media (min-width: 380px) and (max-width: 750px) {
  .login-Modal{
    width: 80% !important;
  }  
}

@media (min-width: 250px) and (max-width: 380px) {
  .login-Modal{
    width: 90% !important;
  }  
  section .contentBx .inputBx button {
    margin-left: 9%;
  }

  .header {
    margin-left: 0px;
  }
  .header h2 {
    font-size: 30px;
  }
  section .contentBx .formBx {
    width: 100%;
  }
  section .contentBx .inputBx input {
    width: 100%;
  }
  section .contentBx .inputBx {
    padding-left: 0px;
  }
}
