.emergency-outer-box {
  align-items: center;
  position: relative;
  height: 820px;
  width: 100%;
  background: repeating-linear-gradient(
    #7f3d3d,
    #7f3d3d 49.9%,
    #f8f8f8 50.1%,
    #f8f8f8 100%
  );
}

.emergency-container {
  background-color: #fff;
  padding: 0px;
  width: 75%;
  border: none;
  margin-left: 12%;
  position: absolute;

  top: 15%;
}

.emergency-container .service-box {
  display: flex;
  align-items: center;
}

.emergency-container .service-box .left {
  width: 70%;
}

.emergency-container .service-box .left .body {
  width: 90%;
  margin: auto;
}

.emergency-container .service-box .left .body h1 {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 51.96px;
  line-height: 160%;
  /* or 83px */
  color: #263544;
  margin-bottom: 50px;
}

.emergency-container .service-box .left .body .form-line {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
}

.emergency-container .service-box .left .body .form-line .element {
  width: 45%;
  display: flex;
  align-items: center;
  padding: 5px 0px 15px;
  border-bottom: 2px solid #ececec;
}

.emergency-container .service-box .left .body .form-line img {
  width: 40px;
  margin-right: 13px;
}
/* 
.emergency-container .service-box .left .body .form-line form {
} */

.emergency-container .service-box .left .body .form-line form label {
  font-style: normal;
  font-weight: 800;
  font-size: 16px;
  line-height: 19px;
  color: #263544;
}

.emergency-container .service-box .left .body .form-line form input {
  border: none;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;

  letter-spacing: -0.01em;

  color: #263544;
  outline: 0;
}

.emergency-container .service-box .left .body .form-line form .form-select {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;
  /* identical to box height, or 26px */

  letter-spacing: -0.01em;
  border: aliceblue;
  outline-color: transparent;
  color: #263544;
}

.emergency-container .service-box .right {
  width: 30%;
}

.emergency-container .service-box .right img {
  width: 100%;
}

.appoint-button-2 {
  background: #7f3d3d;
  box-shadow: 0px 4px 12px rgba(197, 74, 80, 0.5);
  border-radius: 48px;
  width: 189px;
  height: 50px;
  /* Inside Auto Layout */
  border: none;
  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 10px 0px;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: center;

  color: #ffffff;
}
/* .appoint-ment-icon img {
} */
.box-title {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 3vw;
  line-height: 160%;
  /* or 83px */

  color: #263544;
}
/* .appoint-card {
} */
.appoint-card form input {
  border: none;
  font-family: Inter;
  font-style: normal;
  font-weight: 800;
  font-size: 14px;

  /* or 38px */

  letter-spacing: -0.01em;
}

::-webkit-input-placeholder {
  /* Edge */
  color: #263544;
}

.TB {
  border: none;
  font-family: Inter;
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  background: #fff;
  /* or 38px */

  letter-spacing: -0.01em;

  color: #263544;
}

