.slick-slider {
  width: 90%;
  margin: auto;
}
.slick-prev {
  left: -6%;
}

.slick-next {
  right: -4%;
}
.slick-list {
  margin-left: 12px;
  margin-right: 10px;
}
.custom-indicator li {
  width: 23%;
  height: 140px;
}
.custom-indicator {
  bottom: -150px;
  display: flex !important;
  overflow-x: scroll;
}
.custom-indicator::-webkit-scrollbar {
  display: none;
}
.custom-indicator li.slick-active {
  border: 2px solid #00acb2;
}
.pagination-icon {
  width: 110px;
  height: 130px;
  object-fit: cover;
}
@media (min-width: 570px) and (max-width: 680px) {
  .custom-indicator li {
    width: 26%;
  }
}
@media (min-width: 425px) and (max-width: 569px) {
  .custom-indicator li {
    width: 35%;
  }
}
@media (min-width: 250px) and (max-width: 424px) {
  .custom-indicator li {
    width: 63%;
  }
}
