.preloader {
  height: 100vh;
  width: 100%;
  background: #f1f1f6;
  position: relative;
  top: 0;
  left: 0;
  display: grid;
  place-items: center;
  z-index: 9;
}
.loading {
  display: flex;
  align-items: center;
}
h4.text {
  color: #27ddbf;
  font-size: 1.5rem;
  letter-spacing: 4px;
  margin: 0;
}
.spinner {
  height: 2rem;
  width: 2rem;
  border-radius: 50%;
  border: 4px solid #27ddbf;
  border-top-color: transparent;
  margin: 0.8rem;
  animation: spinner 1s linear infinite;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
