.Ambulance-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: auto;
  margin-top: 3%;
}
.Ambulance-header .header-left {
  display: flex;
  width: 50%;
  margin-left: 5%;
  justify-content: flex-start;
}
.Ambulance-header .header-left h5 {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  /* identical to box height */

  color: #262626;
}
.Ambulance-header .header-right {
  display: flex;
  width: 50%;
  justify-content: flex-end;
  justify-content: space-evenly;
  align-items: center;
}
.Ambulance-header .header-right h5 {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  /* identical to box height */

  color: #737373;
}
.Ambulance-header .header-right .dropdown {
  background: #f9f9f9;
  border-radius: 8px;
}
.Ambulance-header .header-right .dropdown button {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  /* identical to box height, or 143% */

  text-align: center;
  font-feature-settings: "salt" on, "liga" off;

  color: #5a5a5a;

  /* Inside Auto Layout */

  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 0px 4px;
}

/*Responsivity*/
@media (min-width: 250px) and (max-width: 450px) {
  .Ambulance-header .header-left h5 {
    margin-left: 10px;
  }
}
@media (min-width: 250px) and (max-width: 750px) {
  .Ambulance-header {
    display: flex;
    width: 100%;
    flex-direction: column;
  }
  .Ambulance-header .header-left {
    display: flex;
    width: 95%;
    margin: auto;
  }
  .Ambulance-header .header-right {
    display: flex;
    width: 95%;
    margin: auto;
    font-size: small;
    flex-wrap: wrap;
  }
  .Ambulance-header .header-right .dropdown button {
    font-size: 9px;
  }
  .Ambulance-header .header-right h5 {
    font-size: 9px;
  }
}
