.EmergencyBanner-Main {
  width: 100%;
  display: flex;
  min-height: 320px;
  background: url("../img/banner.png");
  background-position: center;

  background-size: contain;
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: left top;
}
.EmergencyBanner-Main .EmergencyBanner-body {
  width: 70%;
  margin: auto;
}
.EmergencyBanner-Main .EmergencyBanner-body .EmergencyBanner-heading {
  font-family: Inter;
  font-style: normal;
  font-weight: 800;
  font-size: 40px;
  line-height: 140%;

  text-align: center;
  letter-spacing: -0.01em;
  font-feature-settings: "salt" on, "liga" off;

  color: #ffffff;
}
@media screen and (max-width: 270px) {
  .EmergencyBanner-Main {
    background-position: center;
    background-size: contain;
    background-size: auto 100vh;
    background-repeat: no-repeat;
    background-position: center;
    min-height: 500px;
  }
  .EmergencyBanner-Main .EmergencyBanner-body {
    width: 100%;
    margin: 0;
    align-items: center;
  }
  .EmergencyBanner-Main .EmergencyBanner-body .EmergencyBanner-heading {
    font-weight: 600;
    font-size: 22px;
    line-height: 1.5rem;
    letter-spacing: 0.05em;
    text-align: center;
    margin-top: 53%;
    margin-bottom: 2%;
  }
}
