.Feedback_textheading {
  display: flex;
  flex-direction: column;
}

.FeedbackModal_Heading {
  display: flex;
  flex-direction: row;
}

.FeedbackModal_Heading .Heading {
  display: flex;
  justify-content: flex-end;
  width: 50%;
  font-size: 22px;
}

.FeedbackModal_Heading .Feedback_logo {
  display: flex;
  width: 50%;
}

.Feedback_textheading .Feedback_letters {
  font-size: 15px;
}

.Feedback_div .feedback_heading {
  color: #8e8e8e;
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
}

input[type='radio'] {
  display: none;
}

.Feedback_div .feedback_ratings_images {
  display: flex;
  justify-content: center;
}

.Feedback_div .feedback_ratings_images i {
  font-size: 36px;
  margin-right: 7px;
}

.Feedback_div .feedback_ratings_images i:hover {
  color: rgba(0, 238, 255, 0.904);
  cursor: pointer;
}
.Feedback_div .feedback_ratings_images i.active {
  color: rgba(0, 238, 255, 0.904);
}

.Feedback_category {
  margin-top: 15px;
  margin-bottom: 15px;
}

.Feedback_category .feedback_category {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.Feedback_category .Feedback_categoryheading {
  margin-bottom: 15px;
  color: #8e8e8e;
}

.Feedback_category .feedback_category .feedback_category_block {
  width: 30%;
  height: 45px;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  background-color: #dbd9d9;
  border: 1px solid #8e8e8e;
  border-radius: 5px;
  cursor: pointer;
}

.Feedback_category .feedback_category .feedback_category_block:hover {
  border: 1px solid #34eb0f;
}
