.testimonials-area {
  background: #16292a;
  color: #fff;
  width: 100%;
  padding: 40px 105px;
  margin-bottom: -1px;
}
.testi-quato-icon {
  display: inline-block;
  border-radius: 50%;
  background-color: rgb(225, 36, 84);
  box-shadow: 0px 16px 32px 0px rgba(225, 36, 84, 0.4);
  line-height: 80px;
  margin-bottom: 45px;
  margin-top: 32px;
  text-align: center;
  transition: 0.4s;
}
.testi-quato-icon img {
  display: inline-block;
}
.testi-quato-icon-green {
  background-color: rgb(143, 181, 105);
  box-shadow: 0px 16px 32px 0px rgba(143, 181, 105, 0.4);
  transition: 0.4s;
}
.testimonial-box-2:hover .testi-quato-icon-green {
  background-color: #8f49a0;
  box-shadow: 0px 16px 32px 0px rgba(225, 36, 84, 0.4);
}

.testi-content-2 h1 {
  margin-right: 20px;
}

.testi-content p {
  font-size: 28px;
  line-height: 48px;
  padding: 0 65px;
  margin-bottom: 120px;
}
.testi-author-title {
  font-size: 28px;
}
.testi-author-desination {
  font-weight: 500;
  color: #8fb569;
  text-transform: uppercase;
  letter-spacing: 2px;
  display: block;
  margin-bottom: 25px;
}
.testi-content span {
  height: 70px;
  width: 2px;
  background: #e12454;
  position: absolute;
  bottom: -91px;
  left: 0;
  right: 0;
  margin: auto;
}

.testi-bg-icon {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: -9;
}
.test-author-icon {
  display: inline-block;
  border-radius: 50%;
  background-color: rgb(231, 231, 231);
  box-shadow: 0px 8px 16px 0px rgba(71, 71, 71, 0.2);
}
.testi-box {
  margin-bottom: 212px;
}
.testi-rating-list ul li {
  display: inline-block;
}
.testi-content-2 h3 {
  font-size: 24px;
  line-height: 1.4;
  margin-bottom: 20px;
}
.testi-content-2 p {
  line-height: 26px;
  padding-right: 45px;
  display: inline-flex;
}
.testi-author-icon-2 {
  margin-right: 30px;
}
.testi-author-desination-2 h4 {
  font-size: 20px;
  margin-bottom: 14px;
}
.testi-rating-list ul li i {
  color: #ffae00;
}
.testimonials-activation {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 1200px) {
  .testimonials-activation {
    flex-wrap: wrap;
  }
}
.progressbar {
  border-radius: 24px;
  height: 8px;
  width: 75%;
  display: inline-flex;
  margin-bottom: 20px;
}
.points {
  font-weight: 800;
  font-size: 56px;

  display: inline-flex;
}
.testimon {
  background: white;
  color: black;
  padding: 20px;
  border-radius: 20px;
}
.text {
  display: inline-block;
  margin-left: 2%;
}
.testi-rating-list {
  display: inline-block;
}
@media (min-width: 250px) and (max-width: 450px) {
  .testimonials-area {
    padding: 20px 20px;
  }
  .testi-rating-list .rating {
    font-size: 25px;
  }
  .points {
    font-size: 32px;
  }
  .progressbar {
    width: 95%;
    margin-bottom: 0px;
  }
  .ProgressBarMain {
    display: flex;
    flex-direction: column;
  }

  .text {
    margin-top: 10px;
    margin-bottom: 0px;
    font-size: 12px;
    margin-bottom: 15px;
  }

  .testi-author-desination-2 h4 {
    font-size: 18px;
    margin-bottom: 14px;
  }
  .testi-author-desination-2 span {
    font-size: 12px;
  }
  .testimon {
    padding: 10px 15px;
  }
  .testimon h3 {
    font-size: 14px;
  }
  .testimon p {
    font-size: 10px;
  }

  .testimonial-box-2 {
    margin-top: -100px;
  }
}
@media (min-width: 451px) and (max-width: 630px) {
  .testimonials-area {
    padding: 20px 40px;
  }

  .testimonial-box-2 {
    margin-top: -70px;
  }
}
