.MultipleImageUploader-main {
  display: flex;
  width: 90%;
  margin: auto;
}
.MultipleImageUploader-main .upload__image-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: -15px;
}
.MultipleImageUploader-main .upload__image-wrapper .multi-uploader {
  height: 100px;
  width: 90%;
  padding: 30px;
  flex-direction: row;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background: #f8f8f8;
  border-radius: 10px;
  border: 2px dashed;
  border-color: #9d9d9d;
  margin: auto;
}
.MultipleImageUploader-main .upload__image-wrapper .multi-uploader:hover {
  cursor: pointer;
}
.MultipleImageUploader-main .upload__image-wrapper .multi-uploader .leftpart {
  width: 25%;
}
.MultipleImageUploader-main .upload__image-wrapper .multi-uploader .rightpart {
  width: 75%;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;

  color: #a8a8a8;
}
.MultipleImageUploader-main .upload__image-wrapper .Image-List {
  height: 200px;
  width: 305px;
  border: 1px solid #d0d5dd;
  border-radius: 18px;
  margin-top: 30px;
  background-color: #d0d5dd;
  flex-wrap: wrap;
  display: flex;
}
.MultipleImageUploader-main .upload__image-wrapper .Image-List .image-item {
  display: flex;
  position: relative;
  margin: 1px;
}
.MultipleImageUploader-main .upload__image-wrapper .Image-List .image-item img {
  height: 99px;
  width: 99px;
}
.MultipleImageUploader-main
  .upload__image-wrapper
  .Image-List
  .image-item
  .image-item__btn-wrapper
  button {
  position: absolute;
  right: 2px;
  top: 2px;
  border-radius: 50%;
  color: red;
  border: aliceblue;
  background-color: aliceblue;
  padding: 0px;
  width: 20px;
  height: 20px;
}
.MultipleImageUploader-main
  .upload__image-wrapper
  .Image-List
  .image-item
  .image-item__btn-wrapper
  .show-more-button {
  top: 35px;
  right: 25px;
  font-size: 10px;
  width: 46px;
  height: 37px;
  border-radius: 10px;
}
.Image-Modal .ImageModal-Main {
  background-color: white;
}
.ImageModal-Main {
  width: 100%;
  height: 550px;
}
.Image-Modal .ImageModal-Main .ImageModal-Body {
  height: 550px;
}
