.slick-cloned {
  /* display: none !important; */
}

.facility_section_main {
  width: 100%;
  background-color: #fff;
  padding: 60px 0 10px;
}

.facilitiesSection {
  /* padding: 40px 105px; */
  padding: 10px 45px;
}
.facilitiesSection-Main {
  background-color: white;
}
.facilitiesSection .header {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 38px;
  /* identical to box height */

  display: flex;
  align-items: center;

  color: #262626;
  margin: 4%;
  margin-bottom: 40px;
}

.cards {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.cards .card1 {
  display: flex;
  width: 30%;
}

.card1 {
  margin-right: 40px;
  text-align: left;
  width: 90% !important;
}

.icon5 {
  color: #00acb2;
  margin-bottom: 20px;
}

.title3 {
  /* margin-bottom: 15px; */
  min-height: 60px;
  overflow: hidden;
  text-overflow: ellipsis;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  /* identical to box height */

  display: flex;
  /* align-items: center; */
  letter-spacing: 0.04em;
  text-transform: capitalize;

  color: #e12556;
}

.para3 {
  width: 90%;
  font-size: 15px;
  letter-spacing: 0.07rem;
  text-align: left;
}

.para {
  width: 281px;
  height: 130px;

  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;
  /* or 26px */

  /* font-feature-settings: "pnum" on, "lnum" on; */

  color: #484848;
}

@media only screen and (max-width: 1155px) {
  .facilitiesSection {
    padding: 40px 50px;
  }
  .cards {
    flex-wrap: wrap;
  }

  .card1 {
    margin-top: 20px;
    margin-right: 20px;
    width: 283px;
  }
}

@media only screen and (max-width: 645px) {
  .facilitiesSection {
    padding: 40px 20px;
  }

  .header {
    margin-bottom: 10px;
  }

  .card1 {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 20px;
    padding-left: 20px;
    width: 100%;
    margin-right: 0px;
  }

  .para3 {
    width: 100%;
  }
}
