.DoctorTimings-Main {
    width: 100%;
    margin-left: 30px;
}

.DoctorTimings-Main .DT-Body {
    justify-content: space-between;
    width: 100%;
    display: flex;
    margin: auto;
    flex-wrap: wrap;
}

.DoctorTimings-Main .DT-Body .Time-Item {
    width: 30%;
    flex-direction: column;
    display: flex;
}

.DoctorTimings-Main .DT-Body .Time-Item .Timings-Day {
    font-family: Inter;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #9d9d9d;
    margin-bottom: 5px;
}

.DoctorTimings-Main .DT-Body .Time-Item .Time-Duration {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 15px;
    align-items: center;
}

.DoctorTimings-Main .DT-Body .Time-Item .Time-Duration .Timings-From {
    width: 45%;
}

.doctordetail_Table_heading {
    margin-left: 5px;
    margin-top: 5px;
}


/* .doctordetail_Table {
  width: 105%;
} */

.doctordetail_Table_heading {
    border: #d0d5dd;
}

.main_box_DoctorDetail {
    box-shadow: 1px 1px 1px 1px #d0d5dd;
}

.main_box_border_DoctorDetail {
    border: #d0d5dd;
    width: 98%;
}

.button_doctorDetailPage {
    background-color: #1faf9f;
    /* position: absolute; */
    right: 0px;
    width: 10%;
    font-size: 15px;
    font-weight: 500;
    top: auto;
    text-decoration: none;
    position: relative;
    left: 88%;
    color: #fff;
}

.button_doctorDetailPage:hover {
    background-color: #1faf9f;
    color: #fff;
}

.heading_DoctorDetailInput {
    font-family: Inter;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    margin-left: 25px;
    margin-top: 15px;
    color: #263544;
}

.DoctorDetailPageModalButton {
    background: #fff;
    color: #000;
    border: none;
    outline: none;
}


