.emergency_call {
  width: 50px;
  height: 50px;
  position: fixed;
  background: #8f49a0;
  padding: 8px;
  right: 0px;
  top: 25%;
  border-top-left-radius: 14px;
  border-bottom-left-radius: 14px;
  z-index: 30;
}
.emergency_call a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
}
.emergency_call a:hover {
  color: #fff;
  text-decoration: none;
}

.emergency_call:hover .overlay {
  visibility: visible;
  opacity: 1;
}

.overlay {
  position: absolute;
  transition: all 0.48s;
  opacity: 0;
  visibility: hidden;
  width: 0;
  height: 100%;
  bottom: 0;
  right: 0px;
  font-family: Inter;
  color: whitesmoke;
  padding-top: 15%;
  text-align: center;
  background-color: #8f49a0;
  border-radius: 14px 0 0 14px;
}

.feedbackModal {
  width: 90px;
  top: 129px;
  right: 0px;
  font-family: Inter;
  color: whitesmoke;
  text-align: center;
  background-color: rgb(143, 73, 160);
  border-radius: 5px 0px 0px 5px;
  position: fixed;
}
