.filterLink {
  padding: 0.5rem 0.7rem;
  cursor: pointer;
}
.filterLink:hover {
  background-color: #f1f1f6;
}

.loading_section {
  width: 100%;
  height: 450px;
  display: grid;
  place-items: center;
}

.SearchResult {
  width: 100%;
  margin: auto;
  padding-top: 40px;
  padding-bottom: 80px;
  background: #fff;
}

.SearchResult .body {
  width: 85%;
  margin: auto;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.SearchResult .body .left-1 {
  width: 40%;
}

.SearchResult .body .left-1 .head {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

/* The container <div> - needed to position the dropdown content */
.SearchResult .dropdown-filter {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.SearchResult .dropdown-filter .dropdown-content {
  display: block;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 100px;
  max-height: 300px;
  overflow-y: scroll;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  opacity: 0;
  visibility: hidden;
  transition: all 0.34s;
}
.SearchResult .dropdown-filter .dropdown-content::-webkit-scrollbar {
  width: 5px;
}
.SearchResult .dropdown-filter .dropdown-content::-webkit-scrollbar-track {
  width: 5px;
  background-color: #ddd;
}
.SearchResult .dropdown-filter .dropdown-content::-webkit-scrollbar-thumb {
  width: 5px;
  background-color: #999;
  border-radius: 40px;
}

/* Links inside the dropdown */
.SearchResult .dropdown-filter .dropdown-content a {
  color: rgb(41, 38, 38);
  padding: 13px 16px;
  text-decoration: none;
  display: block;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 13px;
  line-height: 15px;
  cursor: pointer;
}

/* Change color of dropdown links on hover */
.SearchResult .dropdown-filter .dropdown-content a:hover {
  background-color: #f1f1f1;
}

/* Show the dropdown menu on hover */
.SearchResult .dropdown-filter:hover .dropdown-content {
  visibility: visible;
  opacity: 1;
}

.SearchResult .body .left-1 .head .but-1 {
  padding: 5px 15px;
  background: #f9f9f9;
  margin-right: 10px;
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 15px;
  color: #514d4d;
}

.SearchResult .body .left-1 .head .but-1:hover {
  cursor: pointer;
}

.SearchResult .body .left-1 .head .but-1 .but-1-icon {
  font-size: 20px !important;
  margin-top: -4px;
}

.SearchResult .body .right {
  width: 55%;
}
.SearchResult .body .right .map_area {
  width: 100%;
  height: 600px;
}

.SearchResult .body .left-1 .tail {
  margin: 1rem 0;
  max-height: 600px;
  overflow-y: scroll;
}
.SearchResult .body .left-1 .tail::-webkit-scrollbar {
  width: 7px;
}
.SearchResult .body .left-1 .tail::-webkit-scrollbar-track {
  background-color: #f1f1f6;
  border-radius: 40px;
}
.SearchResult .body .left-1 .tail::-webkit-scrollbar-thumb {
  background-color: #1fb6ba;
  border-radius: 40px;
}

/* ------------------Search Card------------------ */

.search-card-link {
  text-decoration: none;
}

.SearchResultCard {
  position: relative;
  width: 100%;
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.32s;
}
.SearchResultCard:hover {
  background-color: #f7f7f7;
  /* cursor: pointer; */
}

.SearchResultCard .leftimg {
  width: 40%;
}

.SearchResultCard .right {
  width: 50%;
}

.SearchResultCard .leftimg img {
  width: 100%;
  border-radius: 10px;
  max-height: 144px;
  min-height: 120px;
  object-fit: cover;
}

.SearchResultCard .leftimg .type {
  position: absolute;
  margin-left: 10px;
  margin-top: 8px;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  background: #ffffff;
  border-radius: 4px;
  padding: 5px 10px;
  color: #262626;
  font-weight: 600;
  z-index: 2;
}

.SearchResultCard .right .hospital-name {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  text-decoration: none;
  color: #262626;
  margin-bottom: 5px;
}

.SearchResultCard .right .rating {
  color: #f3b23e;
}

.SearchResultCard .right .rating .star-icon {
  font-size: 20px;
  margin-bottom: 10px;
}

.SearchResultCard .right .but-2 {
  display: flex;
  align-items: center;
}

.SearchResultCard .right .but-2 .but-call {
  padding: 7px;
  border-radius: 50%;
  background: #caf5cc;
  color: #0e6b12;
  margin-right: 10px;
  position: relative;
}

.SearchResultCard .right .but-2 .but-dir {
  padding: 7px;
  border-radius: 50%;
  background: #ffe4cc;
  color: #fa8a28;
  position: relative;
}
.SearchResultCard .right .but-2 .but-dir .pop_over,
.SearchResultCard .right .but-2 .but-call .pop_over {
  position: absolute;
  padding: 4px 8px;
  margin-left: 8px;
  white-space: nowrap;
  font-size: 0.9rem;
  color: #111;
  font-weight: bold;
  background-color: #f1f1f6;
  border-radius: 4px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.38s;
  z-index: 4;
  cursor: pointer;
}
.pop_top {
  top: -100%;
  left: -100%;
}
.pop_right {
  top: 0;
  left: 100%;
}
.SearchResultCard .right .but-2 .but-call .pop_over a {
  text-decoration: none;
  color: #111;
}
.SearchResultCard .right .but-2 .but-call .pop_over a:hover {
  color: #1fb6ba;
}

.SearchResultCard .right .but-2 .but-call:hover .pop_over,
.SearchResultCard .right .but-2 .but-dir:hover .pop_over {
  visibility: visible;
  opacity: 1;
}
.SearchResultCard .right .but-2 .but-call .pop_over::before,
.SearchResultCard .right .but-2 .but-dir .pop_over::before {
  content: "";
  position: absolute;
  height: 8px;
  width: 8px;
  transform: rotate(45deg) translateX(-50%);
  background-color: #f1f1f6;
}
.pop_top::before {
  top: 100%;
  left: 45%;
}
.pop_right::before {
  top: 50%;
  left: 0;
}

/* --------------------Responsive---------------- */
@media (min-width: 250px) and (max-width: 660px) {
  .SearchResult .body {
    width: 90%;
    margin: auto;
    flex-direction: column;
  }

  .SearchResult .body .left-1 {
    width: 100%;
    margin-bottom: 30px;
  }

  .SearchResult .body .right {
    width: 100%;
  }

  .SearchResultCard .leftimg {
    width: 80%;
  }

  .SearchResultCard .right {
    margin-left: 30px;
  }

  .SearchResultCard .right .hospital-name {
    font-size: 17px;

    color: #262626;
    margin-bottom: 5px;
  }

  .SearchResultCard .right .rating {
    color: #f3b23e;
  }

  .SearchResultCard .right .rating .star-icon {
    margin-bottom: 10px;
  }
}

.distance_showing {
  font-family: Inter;
  font-style: medium;
  font-size: 14px;
  color: #2c2f30;
  padding-left: 20px;
}
