.signup {
  width: 50% !important;
  background-color: #fff;
  padding: 1.5rem 2rem;
  border-radius: 4px;
  box-shadow: 0 0 14px rgba(0, 0, 0, 0.1);
  h1 {
    font-size: 1.6rem;
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 1rem;
  }
  .input_box {
    display: flex;
    flex-direction: column;
    padding: 0.4rem 0;
    input,
    select {
      width: 100% !important;
      margin: 0.4rem 0;
      outline: none;
      border: 1px solid #ccc;
      background-color: #fff !important;
      border-radius: 4px;
      padding: 4px;
      font-size: 1rem;
      transition: all 0.27s;
      &:hover {
        border-color: #999;
      }
      &:focus {
        border-color: #1e90ff;
      }
    }
    button {
      width: 20%;
      padding: 0.4rem 1rem;
      margin: 0.5rem 0;
      outline: none;
      border: none;
      border-radius: 4px;
      color: #fff;
      background-color: #1e90ff;
      font-size: 1.1rem;
    }
  }
}
