@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600&display=swap");

.gap-nothing {
  width: 100%;
  height: 74px;
  background: #16292a;
  margin-top: -1px;
  margin-bottom: -1px;
}

.counter-1 {
  /* padding-top: 50px; */
  background: #16292a;
  height: auto;
  padding-top: 10px;
}

.content-1 {
  /* padding-top: 100px; */
}

.single-couter-1 h1 {
  font-size: 80px;
  color: #e12454;
  margin-top: 20px;
  font-weight: 200;
}
.counter-box-1 h1 {
  font-weight: 600;
  color: #fff;
  font-size: 50px;
  margin-top: 12px;
}
.counter-box-white-1 h1 {
  color: #223645;
}
.single-couter-1 h3 {
  font-size: 24px;
  margin-top: 10px;
  margin-bottom: 17px;
}
.single-couter-1 p {
  margin-bottom: 0;
  padding-right: 40px;
}
.counter-text-1 p {
  width: 100%;
  padding: 0 52px;
}
.counter-box-white-1 h6 {
  position: relative;
  margin-bottom: 11px;
}
.counter-box-white-1 h6::after,
.counter-box-white h6::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 2px;
  left: 0;
  background: #e8ebff;
  right: 0;
  margin: auto;
  bottom: 0;
}
.counter-box-white-1 h6::before {
  background: #8f49a0;
  z-index: 9;
  width: 0px;
  transition: 0.3s;
  visibility: hidden;
  opacity: 0;
}
.counter-box-1.counter-box-white-1:hover h6::before {
  width: 30px;
  visibility: visible;
  opacity: 1;
}

.outer-box {
  /* position: relative; */
  height: auto;
  width: 100%;
  background: repeating-linear-gradient(
    #16292a,
    #16292a 49.9%,
    #f8f8f8 50.1%,
    #f8f8f8 100%
  );
}

.outer-box .container {
  background-color: #fff;
  padding: 0px;
  width: 75%;
  border: none;
}

.outer-box .container .service-box {
  display: flex;
  align-items: center;
}

.outer-box .container .service-box .left {
  width: 70%;
}

.outer-box .container .service-box .left .body {
  width: 90%;
  margin: auto;
}

.outer-box .container .service-box .left .body h1 {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 51.96px;
  line-height: 160%;
  /* or 83px */
  color: #263544;
  margin-bottom: 50px;
}

.outer-box .container .service-box .left .body .form-line {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
}

.outer-box .container .service-box .left .body .form-line .element {
  width: 45%;
  display: flex;
  align-items: center;
  padding: 5px 0px 15px;
  border-bottom: 2px solid #ececec;
}

.outer-box .container .service-box .left .body .form-line img {
  width: 40px;
  margin-right: 13px;
}

.outer-box .container .service-box .left .body .form-line form {
}

.outer-box .container .service-box .left .body .form-line form label {
  font-style: normal;
  font-weight: 800;
  font-size: 16px;
  line-height: 19px;
  color: #263544;
}

.outer-box .container .service-box .left .body .form-line form input {
  border: none;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;

  letter-spacing: -0.01em;

  color: #263544;
  outline: 0;
}

.outer-box .container .service-box .left .body .form-line form .form-select {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;
  /* identical to box height, or 26px */

  letter-spacing: -0.01em;
  border: aliceblue;
  outline-color: transparent;
  color: #263544;
}

.outer-box .container .service-box .right {
  width: 30%;
}

.outer-box .container .service-box .right img {
  width: 100%;
}

.appoint-button {
  background: #1fb6ba;
  box-shadow: 0px 4px 12px rgba(31, 182, 186, 0.5);
  border-radius: 48px;
  width: 189px;
  height: 50px;
  /* Inside Auto Layout */
  border: none;
  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 10px 0px;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: center;

  color: #ffffff;
  transition: all 0.3s;
}

.appoint-button:hover {
  background-color: #fff;
  box-shadow: none;
  border: 2px #1fb6ba solid;
  color: #1fb6ba;
}

.appoint-ment-icon img {
}
.box-title {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 3vw;
  line-height: 160%;
  /* or 83px */

  color: #263544;
}
.appoint-card {
}
.appoint-card form input {
  border: none;
  font-family: Inter;
  font-style: normal;
  font-weight: 800;
  font-size: 14px;

  /* or 38px */

  letter-spacing: -0.01em;
}

::-webkit-input-placeholder {
  /* Edge */
  color: #263544;
}

.TB {
  border: none;
  font-family: Inter;
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  background: #fff;
  /* or 38px */

  letter-spacing: -0.01em;

  color: #263544;
}

