.home2-main {
  width: 100%;
  display: flex;
  min-height: 750px;
  background: url("../img/landing\ banner.jpg");
  background-position: center;

  background-size: contain;
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: left top;
}

.home2-main .home2-body {
  width: 100%;
  display: flex;
  margin: auto;
  flex-direction: column;
}

.home2-main .home2-body .home2-heading {
  display: flex;
  margin: auto;
  font-family: Inter;
  font-style: normal;
  font-weight: 700;
  line-height: 4rem;
  font-size: 56px;
  text-align: center;
  letter-spacing: 0.05em;
  color: #ffffff;
}

.bottom {
  width: 100%;
  margin-top: 30px;
}

.bottom .search-body {
  background-color: white;
  width: 700px;
  margin: auto;
  display: flex;
  border: 2px solid #e4e1e1;
  box-sizing: border-box;
  border-radius: 48px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 15px 40px;
}
.bottom .search-body .search-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.bottom .search-body .search-item .left {
  display: flex;
  flex-direction: column;
}
.bottom .search-body .search-item .left h3 {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  /* identical to box height */

  color: #686868;
}

.bottom .search-body .search-item .left input {
  border: none;
  overflow: auto;
  outline: none;

  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;

  resize: none;

  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14.22px;
  line-height: 17px;

  color: #b0b0b0;
}
.bottom .search-body .search-item .left input::placeholder {
  font-size: 14.22px;
  line-height: 17px;

  color: #b0b0b0;
}

.bottom .search-body .search-item .search-button {
  display: flex;
  align-items: center;
  padding: 7px 15px;
  background: #1f1f1f;
  color: #fff;
  border-radius: 48px;
  cursor: pointer;
  margin-right: -60px;
}
.bottom .search-body .search-item .search-button h4 {
  margin-right: 5px;
  font-weight: 600;
  font-size: 12px;
  margin-top: 7px;
}
.bottom .search-body .search-item .search-button .search-icon {
  font-size: 20px;
}
.bottom .search-body .search-item .search-button .rotate_icon {
  animation: rotate 2s ease-in-out infinite;
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.bottom .search-body .search-item .right {
  width: 35px;
  height: 35px;
  background: #ffe4cc;
  margin-right: 20px;
  border-radius: 50%;
}
.bottom .search-body .search-item .right img {
  margin-left: 12px;
  margin-top: 7px;
  width: 12px;
}

/* suggestion */
.input_suggestion {
  position: relative;
}
.suggestion_dropdown {
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 9;
}
.suggestion_dropdown .drp_res {
  caret-color: transparent;
  cursor: pointer;
  color: #111 !important;
  border: 1px solid #ddd !important;
  background-color: #eee;
  padding: 6px;
}

