.SearchTop {
  width: 100%;
  padding: 40px 0px;
  background-color: #fff;
  margin-top: 0px;
}

.SearchTop .body {
  width: 90%;
  margin: auto;
}

.SearchTop .body .head h2 {
  text-align: center;
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 51.96px;
  line-height: 160%;
  color: #263544;
  margin: auto;
}

.SearchTop .body .bottom {
  width: 100%;
  margin-top: 30px;
}

.SearchTop .body .bottom .search-body {
  width: 51%;
  margin: auto;
  display: flex;
  border: 2px solid #e4e1e1;
  box-sizing: border-box;
  border-radius: 48px;
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  padding: 15px 40px;
}

.SearchTop .body .bottom .search-body .search-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: -30px;
  width: 40%;
}

.SearchTop .body .bottom .search-body .search-item .left {
  display: flex;
  flex-direction: column;
}

.SearchTop .body .bottom .search-body .search-item .left h3 {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 15px;
  /* identical to box height */

  color: #686868;
}

.SearchTop .body .bottom .search-body .search-item .left input {
  border: none;
  overflow: auto;
  outline: none;

  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;

  resize: none;

  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14.22px;
  line-height: 17px;

  color: #b0b0b0;
}

.SearchTop .body .bottom .search-body .search-item .left input::placeholder {
  font-size: 14.22px;
  line-height: 17px;

  color: #b0b0b0;
}

.SearchTop .body .bottom .search-body .search-item .search-button {
  display: flex;
  align-items: center;
  padding: 7px 15px;
  background: #1f1f1f;
  color: #fff;
  border-radius: 48px;
}

.SearchTop .body .bottom .search-body .search-item .search-button:hover {
  cursor: pointer;
}

.SearchTop .body .bottom .search-body .search-item .search-button h4 {
  margin-right: 5px;
  font-weight: 600;
  font-size: 12px;
  margin-top: 7px;
}

.SearchTop .body .bottom .search-body .search-item .search-button .search-icon {
  font-size: 20px;
}

.SearchTop .body .bottom .search-body .search-item .search-button .rotate_icon {
  animation: rotate 2s ease-in-out infinite;
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.SearchTop .body .bottom .search-body .search-item .right {
  width: 35px;
  height: 35px;
  background: #ffe4cc;
  margin-right: 20px;
  margin-left: 50px;
  border-radius: 50%;
}

.SearchTop .body .bottom .search-body .search-item .right img {
  margin-left: 12px;
  margin-top: 7px;
  width: 12px;
}

/* Responsive */
